<script>
	import Config from '../../config.js';
	import sampletmask from '../common/common_mask/samplemask.vue'//
	import enterprisemask from '../common/common_mask/enterprisemask.vue'//
	import approvalmask from '../workflow/approving.vue'
	import flowhistorymask from '../workflow/flowhistory.vue'
	import flowmapmask from '../workflow/flowmap.vue'
	import vewPoplemask from '../workflow/vewPople.vue'
	import categorymask from '../common/common_mask/categorylistmask.vue'//
	import productmask from '../common/common_mask/productlistmask.vue'//
	import teststandardmask from '../common/common_mask/teststandardmask.vue'//
	import testprojectmask from '../common/common_mask/testprojectmask.vue'//
	export default {
		name: 'masks',
		components: {
			 approvalmask,
			 flowhistorymask,
			 flowmapmask,
			 vewPoplemask,
			 sampletmask,
			 enterprisemask,
			 categorymask,
			 productmask,
			 teststandardmask,
			 testprojectmask
		},
		data() {
			var validate = (rule, value, callback) => {
				if(value === '') {
					callback(new Error(''));
				} else {
					callback();
				}
			};
			var validateVname = (rule, value, callback) => {//
                if (this.dataInfo.V_NAME === undefined || this.dataInfo.V_NAME === '' || this.dataInfo.V_NAME === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateVaddress = (rule, value, callback) => {//
                if (this.dataInfo.V_ADDRESS === undefined || this.dataInfo.V_ADDRESS === '' || this.dataInfo.V_ADDRESS === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateVzipcode = (rule, value, callback) => {//
                if (this.dataInfo.V_ZIPCODE === undefined || this.dataInfo.V_ZIPCODE === '' || this.dataInfo.V_ZIPCODE === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateVperson = (rule, value, callback) => {//
                if (this.dataInfo.V_PERSON === undefined || this.dataInfo.V_PERSON === '' || this.dataInfo.V_PERSON === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateVphone = (rule, value, callback) => {//
                if (this.dataInfo.V_PHONE === undefined || this.dataInfo.V_PHONE === '' || this.dataInfo.V_PHONE === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validateItemname = (rule, value, callback) => {//
                if (this.dataInfo.ITEM_NAME === undefined || this.dataInfo.ITEM_NAME === '' || this.dataInfo.ITEM_NAME === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
			var validatePname = (rule, value, callback) => {//
                if (this.dataInfo.P_NAME === undefined || this.dataInfo.P_NAME === '' || this.dataInfo.P_NAME === null) {
                    callback(new Error(''));
                }else {
                    callback();
                }
            };
            //var exp = /^([1-9][\d]{0,7}|0)(\.[\d]{1,2})?$/;
            //
            var price=(rule, value, callback) => {// 
				var exp = /^(-)?\d{1,3}(,\d{3})*(.\d+)?$/;
				if(value != '' && value!=undefined){
					if(exp.test(value)==false){ 
	                    callback(new Error(''));
	              }else{
	                    callback();
	                }
				}else {
					callback();
				}
           };
			return {
				approvingData:{},
				loadSign:true,//
				commentArr:{},
				falg:false,//
				basic_url: Config.dev_url,
				dataInfo: {
					STATUS: '1',
					STATUSDesc:'',
					VERSION:'1',
					TYPE:'2',//
					TYPEDesc:'',
					ITEM_NAME:'',
					VENDOR:'',
					ITEM_NAME:'',
					ITEM_MODEL:'',
					ITEM_QUALITY:'',
					ITEM_SECRECY:'',
					ITEM_METHOD:'',
					ITEM_DISPOSITION:'',
					PRODUCT_TYPE:'',
					PRODUCT:'',
					COMPDATE:'',
					COMPMODE:'',
					REMARKS:'',
					V_NAME:'',
					V_ADDRESS:'',
					V_ZIPCODE:'',
					P_NAME:'',
					INSPECT_PROXY_PROJECList: [],
					INSPECT_PROXY_BASISList: [],
					CHECK_PROXY_CONTRACTList: [],
				},
				gridData: [], //
				page: {
					currentPage: 1,
					pageSize: 10,
					totalCount: 0
				},
				options: [{
					value: '',
					label: ''
				}, {
					value: '',
					label: ''
				}, {
					value: '',
					label: ''
				}, {
					value: '',
					label: ''
				}, {
					value: '',
					label: ''
				}],
				value: '',
				selval:[],
				edit: true, //
				noedit: false,
				editSearch: '', //
				col_but1: true,
				col_but2: true,
				show: false,
				isok1: true,
				isok2: false,
				down: true,
				up: false,
				type: '',
				datainfo:{},
				addtitle: true, //titile
				modifytitle: false, //titile
				viewtitle: false, //title
				views: false,
				noviews: true, //
				modify: false,
				start:false,
				approval:false,
				activeName: 'first',//tabs
				activeNames: ['1', '2', '3', '4', '5', '6', '7', '8', ], //
				labelPosition: 'top', //
				labelPositions: 'right',
				rules: {
					V_NAME: [{ required: true, validator: validateVname}],//
					V_ADDRESS: [{ required: true,validator: validateVaddress}],//
					V_ZIPCODE: [{ required: true,validator: validateVzipcode}],//
					V_PERSON: [{ required: true,validator: validateVperson}],//
					V_PHONE: [{ required: true,validator: validateVphone}],//
					R_VENDOR: [{ required: true, message: '', trigger: 'blur' }],//
					// VENDOR: [{ required: true, message: '', trigger: 'blur' }],//
					P_NAME: [{ required: true,validator: validatePname}],//
					// PRODUCT_UNIT:[{required: true, message: '', trigger: 'blur'}],//
					ITEM_NAME: [{ required: true,validator: validateItemname}],//
					ITEM_ID: [{ required: true, message: '', trigger: 'blur' }],//
					ITEM_MODEL: [{ required: true, message: '', trigger: 'blur' }],//
					ITEM_QUALITY: [{ required: true, message: '', trigger: 'blur'},{ type: 'number', message: ''}],//
//					ITEM_STATUS: [{ required: true, message: '', trigger: 'blur' }],//
					ITEM_SECRECY: [{ required: true, message: '', trigger: 'blur' }],//
					ITEM_METHOD: [{ required: true, message: '', trigger: 'change' }],//
					ITEM_DISPOSITION: [{ required: true, message: '', trigger: 'change' }],//
					REMARKS: [{ required: true, message: '', trigger: 'blur' }],///
					COMPDATE: [{ required: true, message: '', trigger: 'blur' }],//
					PROXYNUM: [{ required: true, message: '', trigger: 'blur' }],//
					REPORT_QUALITY: [{ required: true, message: '', trigger: 'blur' },{ type: 'number', message: ''}],//
					REPORT_MODE: [{ required: true, message: '', trigger: 'change' }],//
					REPORT_FOMAT: [{ required: true, message: '', trigger: 'change' }],//
					MAINGROUP: [{ required: true, message: '', trigger: 'change' }],//
					LEADER: [{ required: true, message: '', trigger: 'blur' }],//
//					MEMO: [{ required: true, message: '', trigger: 'blur' }],//
					CHECK_COST:[{required: false,trigger: 'change',validator:price}],
					ACTUALCOST:[{trigger: 'blur',validator:price}],
					CONTRACTCOST:[{trigger: 'blur',  validator:price}],
				},
				//tree
				resourceData: [], //
				dialogVisibleuser:false,
				customid:"",
				dataid:'',//id
				inspectPro:'inspectPro',//appname
				CUSTOMER_PERSONList:[],//
				maingroup:[],//
				selectData:[],//
				leaderdata:[],//
//				deptid:'',//id
				catenum:'',//
				pronum:'',//
				basisnum:'',////

			};
		},
		methods: {
			//
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},	
			toNum(str) {
				return str.replace(/\,|\/g, "");
			},
			//
			testPrice(item){
				var money = document.getElementById("testprice").value;
				if(money == ''){
					return;
				}else{
					var num = parseFloat(this.toNum(money)).toFixed(2).toString().split(".");
					num[0] = num[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)','ig'),"$1,");
					item.CHECKCOST = num.join(".");
				}
			},	
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD");
			},
			loadMore () {
			   if (this.loadSign) {
			     this.loadSign = false
			     this.page.currentPage++
			     if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
			       return
			     }
			     setTimeout(() => {
			       this.loadSign = true
			     }, 1000)
			     this.requestData()
			   }
			 },	
			toNum(str) {
				return str.replace(/\,|\/g, "");
			},
			//
			toPrice(){
				var money = document.getElementById("cost").value;
				// this.initcost = money;
				var num = parseFloat(this.toNum(money)).toFixed(2).toString().split(".");
				num[0] = num[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)','ig'),"$1,");
				// this.dataInfo.CHECTCOST="" + num.join(".");
				this.dataInfo.CHECK_COST = num.join(".");
			},
			staPrice(){
				var money = document.getElementById("stacost").value;
				// this.initsta = money;
				var num = parseFloat(this.toNum(money)).toFixed(2).toString().split(".");
				num[0] = num[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)','ig'),"$1,");
				// this.dataInfo.CHECTCOST="" + num.join(".");
				this.dataInfo.CONTRACTCOST = num.join(".");
			},
			actualPrice(){
				var money = document.getElementById("actualcost").value;
				// this.initactual = money;
				var num = parseFloat(this.toNum(money)).toFixed(2).toString().split(".");
				num[0] = num[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)','ig'),"$1,");
				// this.dataInfo.CHECTCOST="" + num.join(".");
				this.dataInfo.ACTUALCOST = num.join(".");
			},
			reset() {					
				this.dataInfo={
					VERSION: '1',
					STATUS: '1',
					STATUSDesc:'',
					ITEM_NAME:'',
					VENDOR:'',
					ITEM_NAME:'',
					ITEM_MODEL:'',
					ITEM_QUALITY:'',
					ITEM_SECRECY:'',
					ITEM_METHOD:'',
					ITEM_DISPOSITION:'',
					COMPDATE:'',
					COMPMODE:'',
					REMARKS:'',
					V_NAME:'',
					V_ADDRESS:'',
					V_ZIPCODE:'',
					P_NAME:'',
					INSPECT_PROXY_PROJECList: [],
					INSPECT_PROXY_BASISList: [],
					CHECK_PROXY_CONTRACTList: [],
				};	  
				},
			handleClick(tab, event) {
//		        console.log(tab, event);

		    },
			iconOperation(row, column, cell, event) {
				if(column.property === "iconOperation") {
					row.isEditing = !row.isEditing;
				}
			},
			//
			build(){
				var dataid = this.dataInfo.ID;
					var Url = this.basic_url + '/api-apps/app/inspectPro/operate/createWorkorder?ID='+dataid;
					this.$axios.get(Url, {}).then((res) => {
						if(res.data.resp_code == 0) {
							this.$message({
								message: '',
								type: 'success'
							});
						}else{
							this.$message({
							message: '',
							type: 'warning'
						});
						}
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
			},
			sizeChange(val) {
				this.page.pageSize = val;
				this.requestData();
			},
			currentChange(val) {
				this.page.currentPage = val;
				this.requestData();
			},
			//
			addfieldBasis() {
				var obj = {
					P_NUM: '',
					P_DESC: '',
					REMARKS:'',
					VERSION: '',
					STATUS: '1',
					isEditing: true
				};
				this.dataInfo.INSPECT_PROXY_PROJECList.push(obj);
			},
			addfieldProject() {
				var obj = {
					S_NUM: '',
					S_DESC: '',
					VERSION: '',
					STATUS: '1',
					isEditing: true
				};
				this.dataInfo.INSPECT_PROXY_BASISList.push(obj);
			},
			addcheckProCont() {
				var obj = {
					PROXY_CONTRACT_NUM: '',
					PROXYNUM: '',
					VENDOR: '',
					P_REMARKS: '',
					BASIS: '',
					REQUIRE: '',
					Q_TYPE: '',
					CHECKCOST: '',
					STATUS:'1',
					isEditing: true
				};
				this.dataInfo.CHECK_PROXY_CONTRACTList.push(obj);
			},
			addsample(type){
				this.$refs.samplechild.visible(type);
			},
			
			//
			deleteRow(index,rows) {//Table-
				rows.splice(index,1);

			},
			//
			visible() {
				this.$axios.get(this.basic_url + '/api-user/users/currentMap',{}).then((res)=>{
					this.dataInfo.DEPTID = res.data.deptId;
					this.dataInfo.ENTERBY = res.data.id;
					// this.dataInfo.ORGID = res.data.deptName
					var date = new Date();
                    this.dataInfo.ENTERDATE = this.$moment(date).format("YYYY-MM-DD");
                    this.dataInfo.TYPE = '1';
                    this.dataInfo.TYPEDesc = '';
					this.show = true;
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					})
				})
               	this.addtitle = true;
				this.modifytitle = false;
				this.viewtitle = false;
				this.views = false; //
				this.noviews = true;
				this.edit = true;
				this.noedit = false;
			},
			//
			detailgetData() {
			var url = this.basic_url +'/api-apps/app/inspectPro/' + this.dataid;
				this.$axios.get(url, {}).then((res) => {
					//
					for(var i = 0;i<res.data.INSPECT_PROXY_BASISList.length;i++){
						res.data.INSPECT_PROXY_BASISList[i].isEditing = false;
					}
					//
					for(var m = 0;m<res.data.INSPECT_PROXY_PROJECList.length;m++){
						res.data.INSPECT_PROXY_PROJECList[m].isEditing = false;
					}
					//
					for(var n = 0;n<res.data.CHECK_PROXY_CONTRACTList.length;n++){
						res.data.CHECK_PROXY_CONTRACTList[n].isEditing = false;
					}
					this.dataInfo = res.data;
					this.show = true;
					//
					let _obj = JSON.stringify(this.dataInfo);
        			this.datainfo = JSON.parse(_obj);
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
			},	
			
			// 
			detail(dataid) {
				this.dataid=dataid;
				var usersUrl = this.basic_url + '/api-user/users/currentMap'
				this.$axios.get(usersUrl, {}).then((res) => {
					this.dataInfo.DEPTID = res.data.deptId;//id
					this.dataInfo.CHANGEBY = res.data.id;
					var date = new Date();
					this.dataInfo.CHANGEDATE = this.$moment(date).format("yyyy-MM-dd");
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
				this.detailgetData();
				this.modifytitle = true;
				this.addtitle = false;
				this.viewtitle = false;
				this.noviews = true;
				this.views = false; //
				this.edit = true;
				this.noedit = false;
			},
			//
			modifyversion() {
				this.$refs.dataInfo.validate((valid) => {
					if(valid) {
						var datainfo=JSON.stringify(this.datainfo); 
	 					var dataInfo=JSON.stringify(this.dataInfo);
					 	if(datainfo==dataInfo){
					  	this.$message({
								message: '',
								type: 'warning'
							});
							return false;
					    }else{
							var url = this.basic_url+ '/api-apps/app/inspectPro/operate/upgraded'
							this.$axios.post(url, this.dataInfo).then((res) => {
								//resp_code == 0
								if(res.data.resp_code == 0) {
									this.$message({
										message: '',
										type: 'success'
									});
									//
									this.$emit('request');
									this.show = false;
								}else{
								this.show = true;
								if(res.data.resp_code == 1) {
									//res.data.resp_msg!=''
									if( res.data.resp_msg!=''){
									 	this.$message({
											message: res.data.resp_msg,
											type: 'warning'
									 	});
									}else{
										this.$message({
											message:'',
											type: 'warning'
										});
									}
								}
							}		
							}).catch((err) => {
								this.$message({
									message: '',
									type: 'error'
								});
							});
						}
					} else {
						this.$message({
							message: '',
							type: 'warning'
						});
					}
				});
			},
			//
			view(dataid) {
				this.dataid=dataid;				
				this.modifytitle = false;
				this.addtitle = false;
				this.viewtitle = true;
				this.views = true; //
				this.noviews = false;
				this.edit = true;
				this.noedit = true;
				this.isEditing=false;
				this.detailgetData();
				//
				var url = this.basic_url + '/api-apps/app/inspectPro/flow/isStart/'+dataid;
				this.$axios.get(url, {}).then((res) => {
					if(res.data.resp_code==1){
						this.start=true;
						this.approval=false;
					}else{
						this.start=false;
						this.approval=true;
					}
				});
			},
			addcategory(){//
				this.$refs.categorychild.visible(this.dataInfo.R_VENDOR);
			},
			//
			categorydata(value){
				this.catenum = value[0];
				this.dataInfo.PRODUCT_TYPE  = value[1];
			},
			addproduct(){//
				this.$refs.productchild.visible(this.catenum);
			},
			//
			appenddata(value){
				this.pronum = value[0];
				this.dataInfo.PRODUCT = value[1];
			},
			//
			basisleadbtn(){
				this.$refs.standardchild.basislead(this.pronum);
			},
			 //
			addbasis(value){
				this.basisnum = value[0];
				for(var i = 1;i<value.length;i++){
					value[i].S_DESC = value[i].S_NAME;
					this.dataInfo.INSPECT_PROXY_BASISList.push(value[i]);
				}
				// this.dataInfo.WORK_NOTICE_CHECKBASISList = value;
			},
			//
			basisleadbtn2(){
				this.$refs.projectchild.projectlead(this.basisnum);
			},
			 //
			addproject(value){
				for(var i = 0;i<value.length;i++){
					value[i].P_DESC = value[i].P_NAME;
					this.dataInfo.INSPECT_PROXY_PROJECList.push(value[i]);
				}
				// this.dataInfo.WORK_NOTICE_CHECKPROJECTList = value;
			},
			//
			close() {
				this.show = false;
				this.$emit('request');
			},
			open() {
				this.show = true;
			},
			toggle(e) {
				if(this.isok1 == true) {
					this.maxDialog();
				} else {
					this.rebackDialog();
				}
			},
			maxDialog(e) { //
				this.isok1 = false;
				this.isok2 = true;
				$(".mask_div").width(document.body.clientWidth);
				$(".mask_div").height(document.body.clientHeight - 60);
				$(".mask_div").css("top", "60px");
			},
			//
			rebackDialog() { //
				this.isok1 = true;
				this.isok2 = false;
				$(".mask_div").css("width", "80%");
				$(".mask_div").css("height", "80%");
				$(".mask_div").css("top", "100px");
			},
			//
			appenddes(value){
				this.dataInfo.ITEM_NAME = value;//
			},
			appendmod(value){
				this.dataInfo.ITEM_MODEL=value;
			},
			appendqua(value){
				this.dataInfo.ITEM_QUALITY=value;
			},
			//
			appendname(value){		
				this.dataInfo.V_NAME = value;//
			},
			appendadd(value){
				this.dataInfo.V_ADDRESS=value;
			},
			appendzip(value){
				this.dataInfo.V_ZIPCODE=value;
			},
			appendid(value){
				this.customid=value;
			},
			//
			appendnames(value){
				this.dataInfo.P_NAME=value;
			},
			// users/saveOrUpdate
			save() {
				this.$refs.dataInfo.validate((valid) => {
			        if (valid) {
						if(this.dataInfo.INSPECT_PROXY_BASISList.length<=0&&this.dataInfo.INSPECT_PROXY_PROJECList.length<=0&&this.dataInfo.CHECK_PROXY_CONTRACTList.length<=0){
			        		this.$message({
								message: '',
								type: 'warning'
							});
							return false;
			        	}else{
//							this.dataInfo.ITEM_STATUS=this.dataInfo.ITEM_STATUS==1;
//							this.dataInfo.MESSSTATUS= this.dataInfo.MESSSTATUS==1;//
							var url = this.basic_url + '/api-apps/app/inspectPro/saveOrUpdate';
							this.$axios.post(url, this.dataInfo).then((res) => {
								if(res.data.resp_code == 0) {
									this.$message({
										message: '',
										type: 'success'
									});
									//
									this.$emit('request');
									this.reset();
								}
							}).catch((err) => {
								this.$message({
									message: '',
									type: 'error'
								});
							});
						}
						this.falg = true;
			        }else{
			          	this.show = true;
			            this.$message({
							message: '',
							type: 'warning'
						});
			           this.falg = false;
			        }
				});
			},
			saveAndUpdate(){
				this.save();
				if(this.falg){
					this.show = false;
				}
			},
			saveAndSubmit(){
				this.save();
				this.show = true;			
			},
			handleClose(done) {
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			visablemaingroup:function(callback){    //false ctx.getAreaListDataSearch(vc,1);
			    if(callback){
			    	if(this.dataInfo.R_VENDOR==""||this.dataInfo.R_VENDOR==undefined){
						this.$message({
							message: '',
							type: 'error'
						});
			    	}
			    }	
			},
			visableleader:function(callback){
				 if(callback){
			    	if(this.dataInfo.MAINGROUP==""||this.dataInfo.MAINGROUP==undefined){
						this.$message({
							message: '',
							type: 'error'
						});
			    	}
			    }	
			},
			//
			RVENDORSelect(RVENDORid){
				var url = this.basic_url + '/api-user/depts/findByPid/'+RVENDORid;
					this.$axios.get(url, {}).then((res) => {
						this.maingroup = res.data;
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});
			},
			//
			getmaingroup(maingroupid){
				var url = this.basic_url + '/api-user/users/usersByDept?deptId='+maingroupid;
		   			this.$axios.get(url, {}).then((res) => {
						this.leaderdata = res.data.data;
					}).catch((err) => {
						this.$message({
							message: '',
							type: 'error'
						});
					});		
			},
			//
			getCustomer(type) {
				this.$refs.enterprisechild.visible(type);
			},
			addname(){
				var customid=this.customid;
				if(customid==""||customid=="undenfiend"){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.requestData();	
				}
			},
			requestData(){
				var data = {
						page: this.page.currentPage,
						limit: this.page.pageSize,
					}
					var url = this.basic_url + '/api-apps/app/customer/CUSTOMER/'+ this.customid;
					this.$axios.get(url, {
						params: data
					}).then((res) => {
						this.CUSTOMER_PERSONList = res.data.CUSTOMER_PERSONList;
					});
					this.dialogVisibleuser = true;
			},
			addcusname(){
				if(this.selval.length == 0){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else if(this.selval.length > 1){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.dataInfo.V_PERSON = this.selval[0].PERSON;
					this.dataInfo.V_PHONE = this.selval[0].PHONE;
					this.dialogVisibleuser = false;
				}
			},
			
			SelChange(val) {
				this.selval = val;
			},
			//
			startup(){
				var url = this.basic_url + '/api-apps/app/inspectPro/flow/'+this.dataid;
				this.$axios.get(url, {}).then((res) => {
					if(res.data.resp_code == 1) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
				    }else{
				    	this.$message({
								message:res.data.resp_msg,
								type: 'success'
							});
							this.detailgetData();
							this.start=false;
							this.approval=true;
				    }
				});
			},
			//
			approvals(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.inspectPro;
				 var url = this.basic_url + '/api-apps/app/'+this.inspectPro+'/flow/isEnd/'+this.dataid;
		    		this.$axios.get(url, {}).then((res) => {
		    			if(res.data.resp_code == 0) {
							this.$message({
								message:res.data.resp_msg,
								type: 'warning'
							});
		    			}else{
		    				var url = this.basic_url + '/api-apps/app/'+this.inspectPro+'/flow/isExecute/'+this.dataid;
		    				this.$axios.get(url, {}).then((res) => {
				    			if(res.data.resp_code == 1) {
										this.$message({
											message:res.data.resp_msg,
											type: 'warning'
										});
								}else{
									this.$refs.approvalChild.visible();
								}
		    		});
		    		}
				});
			},
			//
			flowhistory(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.inspectPro;
//				this.$refs.flowhistoryChild.open();
				this.$refs.flowhistoryChild.getdata(this.dataid);
			},
			//
			flowmap(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.inspectPro;
				this.$refs.flowmapChild.getimage();
			},
			//
			viewpepole(){
				this.approvingData.id =this.dataid;
				this.approvingData.app=this.inspectPro;
				this.$refs.vewPopleChild.getvewPople(this.dataid);
			},
			getCompany() {
				var type = "2";
				var url = this.basic_url + '/api-user/depts/treeByType';
				this.$axios.get(url, {
					params: {
						type: type
					},
				}).then((res) => {
					this.selectData = res.data;
				});
			},
		},
		mounted() {
			this.getCompany();
		},
	}
</script>

<style>
@import '../../assets/css/mask-modules.css';
.el-collapse-item__content {
    padding-bottom: 0px;
}
#cost{text-align: right}
#stacost{text-align: right}
#actualcost{text-align: right}
/*.el-form-item__error {
	top: 18%;
    left: 5px;
    background: #FFF;
    padding: 5px 10px;
}*/

</style>


